import React, {Component} from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import ListGroup from 'react-bootstrap/ListGroup';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Tab from 'react-bootstrap/Tab';
import Tabs from 'react-bootstrap/Tabs'; 
import  '../componentsStyle/AdminStyle.css';
import '../componentsStyle/LoaderStyle.css';

import * as firebase from 'firebase';

class Admin extends Component {
    constructor() {
        super();
        this.state = {
            loaded: false,
            schools: null,
            passCheck: false,
            password: null,
            debtSum: '',
            newSchoolName: null,
            newGroupeName: "Надія",
            newDay: "П'ятниця"
        }

        this.addTemes = this.addTemes.bind(this)
        this.delTemes = this.delTemes.bind(this)
        this.addSum = this.addSum.bind(this)
        this.addPay = this.addPay.bind(this)     
        this.onSubmit = this.onSubmit.bind(this) 
        this.changePassword = this.changePassword.bind(this) 
        
        this.newSchool = this.newSchool.bind(this) 
        this.addNewSchool = this.addNewSchool.bind(this) 
        
        this.newGroupe = this.newGroupe.bind(this) 
        this.addNewGroupe = this.addNewGroupe.bind(this) 

        this.newDay = this.newDay.bind(this) 
    }
    componentDidMount() {
        const rootRef = firebase.database().ref();
        const schoolRef = rootRef.child('schools');
        const adminRef = rootRef.child('admin');
        const temesRef = rootRef.child('temes');
        let dbinfo, admininfo, temesInfo;
        schoolRef.on('value', snap =>{
            dbinfo = snap.val();
            this.setState({
                schools: dbinfo,
                loaded: true
            })
        })
        adminRef.on('value', snap =>{
            admininfo = snap.val();
            //console.log(admininfo)
            this.setState({
                adminPassword: admininfo.password
            })
        })
        temesRef.on('value', snap =>{
            temesInfo = snap.val();
            //console.log(admininfo)
            this.setState({
                temes: temesInfo
            })
        })
        
    }
    addTemes(e){
        //console.log('add teme',e.target.value)
        let et = e.target.value;
        let school = et[0];
        let group = et[1];
        let newtemes = 0;
        if(et.length === 3){
            newtemes = Number.parseInt(et[2]) + 1;
        }
        if(et.length === 4){
            newtemes = Number.parseInt(et[2]+et[3]) + 1;
        }
        let link = 'schools/'+school+'/groups/'+group;
        //console.log(link)
        firebase.database().ref(link).update({
            temes_pass: newtemes
        })
    }
    delTemes(e){
        let et = e.target.value;
        let school = et[0];
        let group = et[1];
        let newtemes = 0;
        if(et.length === 3){
            newtemes = Number.parseInt(et[2]) - 1;
        }
        if(et.length === 4){
            newtemes = Number.parseInt(et[2]+et[3]) - 1;
        }
        let link = 'schools/'+school+'/groups/'+group;
        //console.log(link)
        firebase.database().ref(link).update({
            temes_pass: newtemes
        })
    }
    addPay(e){
        //console.log(e.target.value)
        let et = e.target.value;
        let school = et[0];
        let group = et[1];
        let pupil;
        if(et.length === 3){
            pupil = Number.parseInt(et[2]) - 2;
        }
        if(et.length === 4){
            pupil = Number.parseInt(et[2]+et[3]) - 2;
        }
        let link = 'schools/'+school+'/groups/'+group+'/pupils/'+pupil;
        firebase.database().ref(link).update({
            debt: this.state.debtSum
        })
        this.setState({
            debtSum: ''
        })
        //console.log(school, group, pupil, 'DATA')
    }
    addSum(e){
        this.setState({
            debtSum: e.target.value
        })
        //console.log(this.state.debtSum)
    }
    onSubmit(e) {
        e.preventDefault();
        if(this.state.adminPassword === this.state.password){
            this.setState({
                passCheck: true
            })
        }
    }
    changePassword(e) {
        //console.log(e.target.value)
        this.setState({
            password: e.target.value
        })
    }
    newSchool(e){
        this.setState({
            newSchoolName: e.target.value
        })
    }
    addNewSchool(){
        let newSchoolId = 1;
        let canBeAdded = true;
        let link = 'schools/'+newSchoolId;
        console.log(link, this.state.newSchoolName);
        for(let i=0; i<this.state.schools.length; i++){
            if(this.state.schools[i].school === this.state.newSchoolName){
                canBeAdded = false
            }
        }
        if(this.state.newSchoolName !== '' && canBeAdded){
        firebase.database().ref(link).update({
            groups:{'0':{
                        name:'newSchool', 
                        pupils:{'0':{
                            debt:'',
                            name: 'test', 
                            password: 'test', 
                            phone:'test'
                        }
                        },
                        temes_open: 1, 
                        temes_pass: 1
                    }
                },
            school: this.state.newSchoolName,
        })}
    }
    newGroupe(e){
        this.setState({
            newGroupeName: e.target.value
        })
        console.log(this.state.newGroupeName)
    }
    addNewGroupe(){
        const rootRef = firebase.database().ref();
        const schoolRef = rootRef.child('schools');
        let dbinfo, schoolIndex, newGName, day, dayf=0, days=0, dayss=0, currentDay;
        schoolRef.on('value', snap =>{
            dbinfo = snap.val();
            this.setState({
                schools: dbinfo
            })
        })
        for(let i = 0; i < this.state.schools.length; i++){
            if(this.state.schools[i].school === this.state.newGroupeName){
                schoolIndex = i;
            }
        }
        
        for(let j = 0; j < this.state.schools[schoolIndex].groups.length; j++){
            if(this.state.schools[schoolIndex].groups[j].name.length === 6){
                dayss++
            }
            if(this.state.schools[schoolIndex].groups[j].name.length === 7 &&
                this.state.schools[schoolIndex].groups[j].name[6] === 'f'
                ){
                dayf++
            }
            if(this.state.schools[schoolIndex].groups[j].name.length === 7 &&
                this.state.schools[schoolIndex].groups[j].name[6] === 's'
                ){
                days++
            }
            if(this.state.newDay === "П'ятниця"){
                day = 'f';
                currentDay=dayf
            }
            if(this.state.newDay === "Субота"){
                day = '';
                currentDay=dayss
            }
            if(this.state.newDay === "Неділя"){
                day = 's';
                currentDay=days
            }
        }
        newGName = 'SL-10'+(currentDay+1)+day;
        let link = 'schools/'+schoolIndex+'/groups/'+(currentDay);
        console.log(link)
        firebase.database().ref(link).update({
            name: newGName, 
            pupils:{'0':{
                debt:'',
                name: 'test', 
                password: 'test', 
                phone:'test'
            }
            },
            temes_open: 1, 
            temes_pass: 1
        })
    }
    newDay(e){
        this.setState({
            newDay: e.target.value
        })
        console.log(this.state.newDay)
    }
    
    render() {
        let i = 1, newschool = 0, newgroup = 0;
        if(!this.state.loaded && this.state.passCheck===false){
            return <div className="lds-facebook"><div></div><div></div><div></div></div>
        }
        if(this.state.loaded && this.state.passCheck===false){
            return (
                <Form onSubmit={this.onSubmit} className="w-50">
                    <Form.Group controlId="formBasicPassword">
                    <Form.Label >Введіть пароль адміна</Form.Label>
                    <Form.Control type="password" placeholder="Password" onChange={this.changePassword}/>
                    </Form.Group>
                   
                    <Button variant="primary" type="submit">
                        Увійти
                    </Button>
                </Form>
              )
        }
        if(this.state.loaded){
            return (
                <div>
                    <h1 className='ff-c'>Усі школи і гупи</h1>
                    <Tabs defaultActiveKey="home" id="uncontrolled-tab-example">
                        <Tab eventKey="home" title="Групи">

                        {
                        this.state.schools.map(item =>(
                        <div key={item.school}>
                            <h1 className='blue ff-c'>{item.school}</h1>
                            <div>
                                {item.groups.map(item =>(
                                    <div key={item.name}>
                                        <h3 align='left' className='gray ff-c ml-30 mt-20'>
                                            <b>{item.name}</b> 	&nbsp;	&nbsp;
                                            [{item.temes_pass} - занять
                                            <button value={newschool+''+newgroup+''+item.temes_pass} onClick={this.addTemes}>+</button>
                                            <button value={newschool+''+ newgroup+''+item.temes_pass} onClick={this.delTemes}>-</button>
                                            ] (включно з пробним)
                                        </h3>
                                            <ListGroup horizontal >
                                                <ListGroup.Item className='width-50'><b>№</b></ListGroup.Item>
                                                <ListGroup.Item className='width-250'><b>Ім'я і Прізвище</b></ListGroup.Item>
                                                <ListGroup.Item className='width-250'><b>Номер телефону(пароль)</b></ListGroup.Item>
                                                <ListGroup.Item className='width-150'><b>Борг</b></ListGroup.Item>
                                                <ListGroup.Item className='width-250'><b>Виписати борг</b></ListGroup.Item> 
                                            </ListGroup>
                                        {item.pupils.map(breakpoint => (
                                            <ListGroup horizontal key={breakpoint.name}>
                                                <ListGroup.Item className='width-50'>{i++}</ListGroup.Item>
                                                <ListGroup.Item className='width-250'>{breakpoint.name}</ListGroup.Item>
                                                <ListGroup.Item className='width-250'>{breakpoint.password}</ListGroup.Item>
                                                <ListGroup.Item className='width-150'>
                                                    {(breakpoint.debt !== '') ? 
                                                    <span className='red'><b>{breakpoint.debt}</b></span> : 
                                                    <span className='green'><b>оплaчено</b></span>}
                                                </ListGroup.Item>
                                                <ListGroup.Item className='width-250'>
                                                    <input type='text' placeholder='сума боргу' className='width-100' onChange={this.addSum}/>
                                                    <button  value={newschool+''+ newgroup+''+i} onClick={this.addPay}>Додати</button>
                                                </ListGroup.Item>
                                            </ListGroup>
                                        ))}
                                        <div style={{visibility: 'hidden'}}>{i = 1} {newgroup++}</div>
                                    </div>
                                ))}
                            </div>
                            <div style={{visibility: 'hidden'}}>{newschool++}{newgroup = newschool - 1}</div>
                        </div>
                        ))
                        }

                        </Tab>
                        <Tab eventKey="temes" title="Теми">
                            <h1>Теми курсу HTML/CSS</h1>
                            <ListGroup horizontal >
                                <ListGroup.Item className='width-50'><b>№</b></ListGroup.Item>
                                <ListGroup.Item className='width-250'><b>Тема</b></ListGroup.Item>
                                <ListGroup.Item className='width-250'><b>Редагувати</b></ListGroup.Item>
                            </ListGroup>
                            {this.state.temes.map((breakpoint, index) => (
                                <ListGroup horizontal key={index}>
                                    <ListGroup.Item className='width-50'>{index+1}</ListGroup.Item>
                                    <ListGroup.Item className='width-250'>{breakpoint.tema}</ListGroup.Item>
                                    <ListGroup.Item className='width-250'><button><b>Редагувати</b></button></ListGroup.Item>
                                </ListGroup>
                            ))}
                            {/*
                            <label><b>Нaзва теми</b></label><br/>
                            <input type="text" value=""/><br/><br/>

                            <label><b>Посилання на Ютуб</b></label><br/>
                            <input type="text" value=""/><br/><br/>

                            <label><b>Посилання на Диск</b></label><br/>
                            <input type="text" value=""/><br/><br/>

                            <label><b>Посилання на github(зроблене завдання)</b></label><br/>
                            <input type="text" value=""/><br/><br/>

                            <button><b>Додати нову тему</b></button>
                            */}
                        </Tab>
                        <Tab eventKey="newschool" title="Додати новий заклад/групу">
                            <br/>
                            <h5 className="header-back">Адмін може додати новий заклад і групи у ньому, а також пароль для викладача якй вестиме заняття<br/>
                                    Назви груп будуть додаватись автоматично.<br/>
                                    Вчитель зможе добавляти учнів зі свого акаунту.
                            </h5>

                            <div className="w-60 fl">
                                <h5>Інформація/редагування існуючих закладів</h5>

                                <ListGroup horizontal key='one'>
                                    <ListGroup.Item><b>Назва закладу</b></ListGroup.Item>
                                    <ListGroup.Item><b>Кількість груп субота</b></ListGroup.Item>
                                    <ListGroup.Item><b>Кількість груп неділя</b></ListGroup.Item>
                                    <ListGroup.Item><b>Додати групу субота</b></ListGroup.Item>
                                    <ListGroup.Item><b>Додати групу на неділя</b></ListGroup.Item>
                                </ListGroup>
                            </div>
                            <div className="w-40 fl">
                                <h5>Додати новий заклад</h5>
                                
                                <label><b>Нaзва нового закладу</b></label><br/>
                                <input type="text" onChange={this.newSchool}/>
                                <button onClick={this.addNewSchool}>Додати</button>
                                <br/><br/>

                                <h5>Додати нову групу у заклад</h5>

                                <label><b>День занять</b></label><br/>
                                <select onChange={this.newGroupe}>
                                    {this.state.schools.map((schools, index)=>(
                                            <option key={index}>{schools.school}</option>
                                        ))
                                    }
                                </select>
                                <select onChange={this.newDay}>
                                    <option>П'ятниця</option>
                                    <option>Субота</option>
                                    <option>Неділя</option>
                                </select>
                                <button onClick={this.addNewGroupe}><b>Додати</b></button>
                            </div>
                        </Tab>
                    </Tabs>
                    
                    
                </div>
            );
        }
    }
}
export default Admin;